.book-info {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    width: 100%;
    gap: 80px;
    margin-top: 20px;
    margin-bottom: 60px;
}

.book-info img{
  width: 200px;
  height: auto;
}

.book-info p{
    font-size: 18px;
    line-height: 30px;
    text-align: justify;
    background-color: rgba(214, 145, 105, 0.5);
    padding: 20px;
    width: 1000px;
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 10px );
}

.book-info h2 {
    font-style: italic;
}

.book-info button {
    padding: 16px;
    font-size: 18px;
    border: none;
    color: white;
}

.buttons {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    gap: 10px;
}

.insta {
    background-color: #c0007a;
}

.amazon {
    background-color: #ff5341;
}

.books {
    margin-bottom: 10px;
    width: 100%;
}

.info {
    display: flex;
    flex-direction: column;
}

