.search {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 15px;
}

.search-general {
  margin-bottom: 10px;
  width: 100%;
}

.search label {
   gap: 10px;
   font-size: x-large;
   font-weight: bolder;
}

.search input{
    padding: 18px;
    margin-left: 1%;
    margin-top: 5%;
}

.book-search {
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    width: 100%;
    gap: 30px;
}

.book-search h2 {
  font-style: italic;
}

.book-search img {
  width: 200px;
  height: auto;
}

.book-search p{
  font-size: 18px;
    line-height: 30px;
    text-align: justify;
    background-color: rgba(214, 145, 105, 0.5);
    padding: 20px;
    width: 1000px;
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 10px );
}

.book-search button {
    padding: 16px;
    font-size: 18px;
    border: none;
    color: white;
}

.buttons-search {
  display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    gap: 10px;
}
